.listContainer {
  @include container;
  margin: 10vh 20px;
  &-noEmployeesMsg {
    color: $dark;
    font-family: $secondary_font;
    padding: 50px 0;
    text-align: center;
    font-weight: 400;
    font-size: 1.5em;
  }
  &-link {
    font-family: $secondary_font;
    text-decoration: none;
    font-weight: 500;
    background-color: $sand;
    color: white;
    border-radius: 2px;
    margin: 1em 1.25rem;
    padding: 1em 1.25rem;
    box-shadow: inset 0 0 0 0 $dark;
    transition: color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
  }
  &-link:hover {
    box-shadow: inset 100px 0 0 0 $dark;
    cursor: pointer;
  }
  &-content {
    padding: 30px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    //SEARCH
    //TABLE
    &-table {
      width: 100%;
      th {
        color: $dark;
        padding: 5px 10px;
      }
    }
  }
}
